html {
  font-size: 62.5%;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif, Arial, Helvetica, sans-serif;
}

*, *:before, *:after {
  box-sizing: inherit;
}

* {
  font-size: 1.7rem;
}

.contenedor {
  width: 94%;
  margin: 0 auto;
  max-width: 1200px;
}

h1 {
  font-size: 4rem;
  font-weight: 700;
}

h2 {
  font-size: 3rem;
  font-weight: 600;
}

h3 {
  font-size: 2rem;
  font-weight: 600;
}

h4 {
  font-size: 1.8rem;
  font-weight: 400;
}

p {
  font-weight: 400;
}

span {
  font-weight: 600;
}

img {
  width: 100%;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.centrar-seccion {
  width: 50%;
  margin: 0 auto;
}

.btn-verde {
  background-color: #06e16f;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline;
  border: none;
  font-size: 2rem;
}
.btn-verde:hover {
  background-color: #05c863;
  cursor: pointer;
}

.btn-rojo {
  background-color: #ff504d;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline;
  border: none;
  font-size: 2rem;
}
.btn-rojo:hover {
  background-color: #ff3734;
  cursor: pointer;
}

.btn-naranja {
  background-color: #ff961f;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline;
  border: none;
  font-size: 2rem;
}
.btn-naranja:hover {
  background-color: #ff8a06;
  cursor: pointer;
}

.btn-azul {
  background-color: #005cb5;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline;
  border: none;
  font-size: 2rem;
}
.btn-azul:hover {
  background-color: #004f9c;
  cursor: pointer;
}

.formulario label {
  display: block;
  margin: 2rem 0;
}
.formulario textarea,
.formulario input:not([type=submit]) {
  border: none;
  border-bottom: 0.4rem solid #005cb5;
  padding: 1.5rem 1rem 1rem 1rem;
  resize: none;
  outline: none;
  width: 100%;
}
.formulario select {
  display: block;
}
.formulario textarea {
  height: 10rem;
}